<template>
  <div>
    <main> <!-- Début du main -->
        <form class="admin">
          <h1>Editer "{{getPerson.name}}"</h1>
          <label>Nom: <input v-model="person.name"></label><br>
          <label>Image: <imageInput v-model="person.img" /></label><br>
          <label>Date: <input v-model="person.date"></label>
          <h3>Biographie</h3>
          <editor v-model="person.bio"
          :options="defaultOptions"
          height="700px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="bio"/>
          <h3>Notes</h3>
          <editor v-model="person.notes"
          :options="defaultOptions"
          height="300px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="notes"/>
          <StateSelector v-model="person.state"/>
          <input type="button" value="Annuler" @click="$router.push($route.path.replace('/edit',''))">
          <input type="button" value="Enregistrer" @click="save()">
          <BarLoader v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
        </form>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import StateSelector from '@/components/state/Selector.vue'
import { mapActions, mapGetters } from 'vuex'
import { Editor } from '@toast-ui/vue-editor'
import ImageInput from '@/components/imageInput.vue'

export default {
  name: 'home',
  components: {
    StateSelector,
    Editor,
    ImageInput
  },
  data () {
    return {
        person: {},
        loading: false,
        defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: false,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchPerson(this.$route.params.person)
    .then(()=>{
        this.person.name = this.getPerson.name
        this.person.state = this.getPerson.state
        this.person.img = this.getPerson.img
        this.person.date = this.getPerson.date
        this.person.bio = this.getPerson.bio,
        this.person.notes = this.getPerson.notes
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('persons',[
        'getPerson',
        'personLoading'
        ])
  },
  methods: {
    ...mapActions('persons',[
      'fetchPerson',
      'updatePerson'
    ]),
    ...mapActions('alert',[
      'newAlert'
    ]),
    getHtml() {
        return this.$refs.Editor.invoke('getMarkdown')
        // return this.$refs.Editor.invoke('getHtml') -> check github @toast-ui.vue-editor issues, if getHtml method works
    },
    save() {
      this.loading = true;
        this.updatePerson({
            slug: this.getPerson.slug,
            name: this.person.name, 
            state: this.person.state, 
            img: this.person.img, 
            date: this.person.date, 
            bio: this.$refs.bio.invoke('getMarkdown'),
            notes: this.$refs.notes.invoke('getMarkdown')
            })
            .then(()=>{
                this.newAlert({type:'success', content:'Successfully edited'})
                this.$router.push('/'+this.getPerson.slug)
            })
            .catch(e=>{
                this.newAlert({type:'error', content:e.response.data})
            })
    }
  }
}
</script>
